<style>
  #login_wrap{
    height: 100%;
    background: linear-gradient(45deg,rgba(254,172,94,0.5),rgba(199,121,208,0.5),rgba(75,192,200,0.5));
  }
  #login_wrap {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  #login_form {
    border-radius: 4px;
    padding: 10px;
    /*background: #E9EEF3;*/
    width: 500px;
    height: 400px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)
  }

  #login_wrap button {
    display: inline-block;
    text-align: center;
  }

  #title {
    text-align: center;
  }

  #login_form button {
    width: 100px;
  }
</style>
<template>
  <div id="login_wrap">
    <el-form id="login_form" label-position="right">
      <h2 id="title"> CRM登录系统 </h2>
      <el-form-item label="账号" style="margin-top: 50px">
        <el-input v-model="user.username" placeholder="请输入账号"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input v-model="user.password" show-password placeholder="请输入密码"></el-input>
      </el-form-item>
      <el-form-item style="margin-top: 50px">
        <el-button @click="doLogin" style="float:left;margin-left: 100px" :loading="button">
          登录
        </el-button>
        <el-button @click="doResgiter" style="float:right;margin-right: 100px">
          注册
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import axios from "axios";
import { Message } from 'element-ui';
import {getMenuById} from '../../global/api.js';
import {getParameterInitList} from '../../global/api/systemApi.js';
var $this = {};
export default {
    data() {
      return {
        user: {
          username: '',
          password: '',
        },
        button:false
      }
    },
    beforeCreate() {
      $this = this;
    },
    methods: {
      doLogin() {
        $this.button = true
        axios.post('http://120.78.13.233:81/login',{
            username:this.user.username,
            password:this.user.password,
        }).then(
          (data) => {
              // 登录成功将token存储到vuex 方便全局调用
              if (data.data.code === 200){
                // 存储token
                this.$store.commit('setToken',data.data.data.token)
                // 存储用户信息
                this.$store.commit('setUserInfo',data.data.data.userInfo)
                // 顺便把菜单栏的请求到Vuex里面方便调用
                getMenuById(parseInt(this.$store.state.userId)).then(res => {
                    this.$store.commit('setMenuLists' ,res.data);
                });
                // 顺便请求系统参数到Vuex里面方便调用
                getParameterInitList().then(res => {
                   this.$store.commit('setParameter' ,res.data.list);
                })
                this.$message({
                message  : '登录成功',
                type     : "success",
                duration : 1500,
                onClose: function (){
                  $this.button = false
                  $this.$router.push('index').catch(err => {
                    console.log(err);
                  });
                }
               })
              }else {
               this.$message({
                 message  : data.data.msg,
                 type     : "error",
                 duration : 1500,
               })
               $this.button = false
              }
          }
        )
      },
      doResgiter() {
        this.$message.success('注册')
      }
    }
  }
</script>




